.product-card{position:relative;width:100%;margin-bottom:20px}.product-link{display:block;text-decoration:none;color:inherit}.product-image-container{position:relative;overflow:hidden;border-radius:8px;margin-bottom:12px;background-color:#f5f5f5;aspect-ratio:672/990}.product-image-wrapper{position:relative;width:100%;height:100%;overflow:hidden}.product-image{width:100%;height:100%;-o-object-fit:cover;object-fit:cover;transition:opacity .5s ease}.primary-image{opacity:1}.hover-image{position:absolute;top:0;left:0;opacity:0}.image-hover .primary-image{opacity:0}.image-hover .hover-image{opacity:1}.product-badge{position:absolute;top:12px;right:12px;border-radius:100px;background-color:#4142df;color:#fff;font-weight:600;font-size:1rem;padding:4px 10px;z-index:1}.product-rating{position:absolute;top:12px;left:12px;border-radius:4px;padding:3px 8px;font-size:14px;font-weight:500;color:#000;display:flex;align-items:center;gap:2px;z-index:1}.rating-score{font-weight:600}.rating-star{color:#e9b52f}.review-count{color:#2f5acf;font-size:12px}.quick-size-options{position:absolute;bottom:0;left:0;width:100%;background-color:hsla(0,0%,100%,.95);padding:12px;transform:translateY(0);transition:transform .3s ease;z-index:2}.size-label{display:block;font-size:14px;font-weight:500;margin-bottom:8px;text-align:center}.size-buttons{display:flex;justify-content:center;gap:8px}.size-button{padding:6px 12px;border:1px solid #ddd;background-color:#fff;border-radius:4px;font-size:14px;cursor:pointer;transition:all .2s ease}.size-button:hover{background-color:#f1f1f1}.product-promo{position:absolute;bottom:0;left:0;width:100%;z-index:1;display:flex;justify-content:center}.promo-image{width:100%;height:auto;-o-object-fit:cover;object-fit:cover}.promo-text{color:#333}.product-info{padding:0 4px}.color-options{display:flex;gap:8px;margin-bottom:8px}.color-option{width:37px;height:18px;border-radius:16px;border:1px solid #ddd;display:inline-block;cursor:pointer;box-shadow:0 1px 3px rgba(0,0,0,.